<template>
  <div class="take">
    <Form :model="formItem">
      <div class="bg-img">
        <div class="title">
          <img src="http://app-image-test.cunyougo.com/active/fabu.png" alt="">
        </div>

        <div class="name">
          <span>菜名</span>
          <span>|</span>
          <input type="text" maxlength="10" v-model="formItem.name">
        </div>
        <div :class="[label_type==2?'des height':'des']">
          <div class="des-title">作品介绍</div>
          <ul>
            <li @click="choseLabel(2)">
              <div :class="[label_type==2?'radio active':'radio']"></div>
              <label>语音介绍</label>
            </li>
            <div v-if="label_type==2" style="display: flex;align-items: center;">
              <li @click="start" class="get-radio"></li>
              <li @click="" class="radio-info"></li>
              <p class="delete">删除</p>
            </div>
            <li @click="choseLabel(1)">
              <div :class="[label_type==1?'radio active':'radio']"></div>
              <label>文字介绍</label>
            </li>
          </ul>
        </div>
        <div class="textarea" v-if="label_type==1">
          <textarea name="" id="texta" maxlength="150" v-model="formItem.des"></textarea>
          <span>(0/150)</span>
        </div>
        <div class="need-food">
          <div class="need-title">
            <span>所需食材</span>
            <span>|</span>
          </div>
          <textarea name="" id="textas" style="resize:none;margin-left: 8px;background: rgba(0, 0, 0, 0);" rows="2.5"
                    maxlength="150" v-model="formItem.need_food"></textarea>
        </div>
        <div class="type">
          <div class="type-title">参赛类别</div>
          <select class="select" v-model="select1">
            <option disabled value="">请选择</option>
            <option value="1">猪头</option>
            <option value="2">猪身</option>
            <option value="3">猪腿</option>
            <option value="4">猪内脏</option>
          </select>
          <select class="select" v-model="select2">
            <option disabled value="">请选择</option>
            <option :value="item.id" v-for="item in smallList">{{item.name}}</option>
          </select>
        </div>
        <div class="image">
          <div class="image-title">菜品照片</div>
          <div class="upload-img"></div>
        </div>
        <div class="author-name">
          <span>作者昵称</span>
          <span>|</span>
          <input type="text" maxlength="10" v-model="formItem.author_name">
        </div>
        <div class="author-name">
          <span>联系方式</span>
          <span>|</span>
          <input type="text" maxlength="11" v-model="formItem.author_phone">
        </div>
        <div class="author">
          <div class="image-title">作者头像</div>
          <Upload
            ref="upload"
            :on-success="handleSuccess"
            :format="['jpg','jpeg','png']"
            :max-size="2048"
            name="img"
            action="http://127.0.0.1:8000/upload/"
          >
            <div class="author-img" :style="{backgroundImage: 'url(' + imgUrl + ')'}"></div>

          </Upload>

        </div>

      </div>
      <div class="footer" @click="handleSubmit(formItem)">
        <img src="http://app-image-test.cunyougo.com/active/tijiao.png" class="footer-img" alt="">
      </div>
    </Form>
  </div>

</template>
<!--<link rel="stylesheet" href="//unpkg.com/iview/dist/styles/iview.css">-->
<script>
  import axios from 'axios'
  import wx from 'weixin-js-sdk'

  export default {
    name: "take",
    data() {
      return {
        select1: "",
        select2: "",
        smallList: [],
        label_type: 1,
        imgUrl: "http://app-image-test.cunyougo.com/active/small_dianji.png",
        formItem: {
          name: "1",
          des: "1",
          need_food: "1",
          author_name: "1",
          author_phone: "18819414573",
        },
        localId: '',
        serverId: '',
        downLoadId: '',
        Soff: true,
        time: 0,
        timer: null
      }
    },
    watch: {
      select1(val) {
        console.log(this.select1);
        if (val == 1) {
          this.smallList = [
            {id: 1, name: '猪头肉'},
            {id: 2, name: '猪嘴'},
            {id: 3, name: '猪耳朵'},
            {id: 4, name: '猪脑'},
          ]
        } else if (val == 2) {
          this.smallList = [
            {id: 5, name: '猪五花'},
            {id: 6, name: '猪里脊'},
            {id: 7, name: '猪排'},
            {id: 8, name: '猪骨'},
            {id: 9, name: '猪尾巴'},
          ]
        } else if (val == 3) {
          this.smallList = [
            {id: 10, name: '猪爪'},
            {id: 11, name: '肘子'},
            {id: 12, name: '蹄膀'},

          ]
        }
        else if (val == 4) {
          this.smallList = [
            {id: 13, name: '猪肝'},
            {id: 14, name: '猪心'},
            {id: 15, name: '猪肚'},
            {id: 16, name: '猪腰'},
            {id: 17, name: '猪肠'},
          ]
        }
      }
    },
    created() {
      var apiUrl = window.location.href;
      axios.get(this.COMMON.httpUrl2 + 'getWechatInfo/', {params: {url: apiUrl}}).then((res) => {
        alert(res.data.appid);
        wx.config({
          debug: true,
          appId: "",
          timestamp: res.data.timestamp,
          noncestr: res.data.noncestr,
          signature: res.data.sign,
          jsApiList: ['startRecord', 'stopRecord', 'playVoice', 'pauseVoice', 'stopVoice', 'uploadVoice', 'downloadVoice', 'downloadVoice']
        });
        wx.ready(() => {
          console.log("jsapi配置成功");
        });
        wx.error((res) => {
          alert("jsapi配置错误", res);
        })
      })
    },
    mounted() {
      console.log(window.location.href);
    },
    methods: {
      handleSubmit(val) {
        console.log(val);
        console.log(this.select1);
        console.log(this.select2);
        var filter = /^(((13[0-9]{1})|(14[5,6,9]{1})|(15[0-9]{1})|(16[6|7]{1})|(17[0-9]{1})|(18[0-9]{1})|(19[8|9]{1}))+\d{8})$/;
        if (val.name == "" || val.des == "" || val.need_food == "" || val.auther_name == "" || val.auther_phone == "") {
          this.$Message.error('提交失败，数据不完整！');
        }
        else if (this.select1 == "" || this.select2 == "") {
          this.$Message.error('提交失败，请选择参赛类别！');
        } else if (!(filter.test(val.author_phone))) {
          this.$Message.error('手机号格式错误，请重新输入！');
        } else {
          val['type'] = parseInt(this.select1);
          val['small_type'] = this.select2;
          val['author_img'] = this.imgUrl;
          val['food_img'] = this.imgUrl;
          val['openid'] = 1;
          console.log(val);
          axios.get('http://127.0.0.1:8000/checkout/', {
            params: {
              type: parseInt(this.select1),
              small_type: this.select2,
              openid: 1
            }
          }).then((res) => {
            console.log(res);
            if (res.data.result == 'ok') {
              axios.post('http://127.0.0.1:8000/addWorkInfo/', JSON.stringify(val),)
                .then((res) => {
                  console.log(res.data);
                  if (res.data.result == 'ok') {
                    this.$router.push({path: '/'})
                  }

                })
            } else {
              this.$Message.error('你已发布过此类型的菜！请更换类型！');
            }
          });

        }

      },
      choseLabel(val) {
        this.label_type = val
      },
      handleSuccess(res) {
        this.imgUrl = res.imgUrl
      },
      start(e) {
        let that = this
        that.time = 0;
        wx.startRecord({
          success: function () {
            // alert('成功调起录音')
            that.timer = setInterval(() => {
              that.time++
            }, 1000);
            that.vicoeEnd()
          },
          cancel: function () {
            alert('用户拒绝授权录音')
          }
        })
      },
      vicoeEnd() {
        let that = this;
        wx.onVoiceRecordEnd({
          // 录音时间超过一分钟没有停止的时候会执行 complete 回调
          complete: function (res) {
            alert('60秒停止录音');
            that.localId = res.localId;
            clearInterval(that.timer)
            // that.upVoice()
          }
        })
      },


    }
  }
</script>

