    background: url("http://app-image-test.cunyougo.com/active/changan.png") no-repeat center center;
    background-size: 100% 100%;
    margin: 10px 0 15px 20px;
  }

  .delete {
    font-size: 14px;
    font-weight: 400;
    text-decoration: underline;
