<template>
  <div class="detail">
    <div class="bg-img">
      <div class="title">
        <span>{{workObj.name}}</span>
      </div>
      <div class="product-detail">
        <img :src="workObj.food_img" alt="">
        <div class="author-info">
          <div class="avater-info">
            <img class="avater-img" :src="workObj.user.avater" alt="">
            <p class="avater-name">{{workObj.user.name}}</p>
          </div>
          <div class="avater-info">
            <p class="zan-num">{{workObj.point_num}}</p>
            <img id="zan" src="http://app-image-test.cunyougo.com/active/zan-9.png" alt="">
          </div>
        </div>
      </div>
      <div class="work-introduce">
        <div>作品介绍</div>
        <div>
          {{workObj.introduce}}
        </div>
      </div>
      <div class="need-food">
        <div>所需食材</div>
        <div>
          {{workObj.food}}
        </div>
      </div>
      <!--<div class="left-img"></div>-->
      <div class="dianzan" id="button" @click="shield()" :style="{backgroundImage: 'url(' + imgUrl + ')'}"></div>
      <div id="test" v-if="show"></div>
      <div id="log_window" v-if="show">
        <div id="icon" @click="cancel_shield()"></div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "detail",
    data() {
      return {
        workObj: {},
        show: false,
        imgUrl: "http://app-image-test.cunyougo.com/active/button3.gif",
        isGives: false
      }
    },
    mounted() {
      var id = this.$router.currentRoute.query.id;
      this.getWorkDetail(id)
    },
    methods: {
      getWorkDetail(id) {
        axios.get(this.COMMON.httpUrl + 'MoreWorkInfo/', {params: {id: id}})
          .then((res) => {
            console.log(res.data);
            if (res.data) {
              this.workObj = res.data.results[0]
            } else {
              console.log("错误信息");
            }
          })
      },
      shield() {
        var val = {
          openid: "1",
          work: this.workObj.id
        };
        this.isGive();
        if (this.isGives) {
          this.$Message.error("您已赞过该菜品，明天再来哟！")
        } else {
          axios.post(this.COMMON.httpUrl2+ 'giveZan/', JSON.stringify(val),)
            .then((res) => {
              console.log(res.data);
              if (res.data.result == 'ok') {
                this.show = true;
                this.imgUrl = "http://app-image-test.cunyougo.com/active/button1.png";
                this.workObj.point_num += 1
              }
            })
        }

      },
      isGive() {
        axios.get(this.COMMON.httpUrl2+"giveZan/", {
          params: {
            openid: 1,
            work: this.workObj.id
          }
        }).then((res) => {
          console.log(res.data.result);
          if (res.data.result == "error") {
            this.isGives = true
          } else {
            this.isGives = true

          }
        })
      },
      cancel_shield() {
        this.show = false
      }
    }
  }
</script>


<style scoped>
  body {
    margin: 0;
  }

  .bg-img {
    width: 100%;
    height: 878px;
    background: url("http://app-image-test.cunyougo.com/active/bg-5.png") no-repeat center;
    background-size: 100% 100%;
  }

  .title {
    font-size: 22px;
    font-weight: 400;
    color:rgba(15,76,86,1);
    text-align: center;
    position: relative;
    top: 84px;
    left: 0;
  }

  .product-detail {
    width: 275px;
    height: 320px;
    margin: auto;
    margin-top: 121px;

  }

  .product-detail img {
    width: 275px;
    height: 275px;
  }

  .author-info {

    height: 46px;
    background:rgba(255,228,152,1);

    display: flex;
    justify-content: space-between;
    padding: 0 10px;

  }

  .author-info .avater-img {
    width: 25px;
    height: 25px;
    border-radius: 50%;

  }

  .avater-info {
    display: flex;
    flex-direction: row;
    align-items: center
  }

  .avater-name {
    font-size: 16px;
    font-weight: 400;
    color:rgba(119,120,43,1);
    margin-left: 6px
  }

  .zan-num {
    font-size: 15px;
    font-weight: 400;
    color:rgba(119,120,43,1);
    margin-right: 5px
  }

  #zan {
    width: 15px;
    height: 14px;
  }

  .work-introduce {
    width: 275px;
    height: 158px;
    background: rgba(0,0,0,0.2);
    margin: auto;
    margin-top: 15px;
    text-align: -webkit-auto;

  }

  .work-introduce div:first-child {
    font-size: 19px;
    font-weight: 400;
    color: rgba(253, 194, 101, 1);
    margin: 0px 0px 9px 18px;
    padding-top: 8px;

  }

  .work-introduce div:last-child {
    font-size: 15px;
    font-weight: 400;
    color: rgba(255, 223, 173, 1);
    margin: 0px 11px 19px 18px;
  }

  .need-food {
    width: 275px;
    height: 80px;
    background: rgba(0,0,0,0.2);
    background: rgba(0,0,0,0.2);
    margin: auto;
    margin-top: 15px;
    text-align: -webkit-auto;
  }

  .need-food div:first-child {
    font-size: 19px;
    font-weight: 400;
    color: rgba(253, 194, 101, 1);
    margin: 0px 0px 9px 18px;
    padding-top: 8px;
  }

  .need-food div:last-child {
    font-size: 15px;
    font-weight: 400;
    color: rgba(255, 223, 173, 1);
    margin: 0px 11px 19px 18px;
  }

  .left-img {
    width: 80px;
    height: 106px;
    background: url("http://app-image-test.cunyougo.com/active/zhu8.png") no-repeat center;
    background-size: 100% 100%;
  }

  .dianzan {
    width: 150px;
    height: 90px;
    background: url("http://app-image-test.cunyougo.com/active/button3.gif") no-repeat center;
    background-size: 100% 100%;
    position: absolute;
    left: 30%;
    top: 775px;

  }

  .clicked {
    width: 150px;
    height: 90px;
    background: url("http://app-image-test.cunyougo.com/active/button1.png") no-repeat center;
    background-size: 100% 100%;
    position: absolute;
    left: 30%;
    top: 775px;

  }

  #test {
    width: 100%;
    height: 878px;
    background-color: #000;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    opacity: 0.8;
    /*兼容IE8及以下版本浏览器*/
    filter: alpha(opacity=30);
  }

  #log_window {
    background: url("http://app-image-test.cunyougo.com/active/mutong2.png") no-repeat center center;
    width: 391px;
    height: 342px;
    background-size: 100% 100%;
    position: fixed;
    z-index: 3;
    top: 90px;
    bottom: 0px;
    left: 15px;
    right: 0;
  }

  #icon {
    background: url("http://app-image-test.cunyougo.com/active/close2.png") no-repeat center center;
    position: fixed;
    z-index: 3;
    top: 375px;
    bottom: 0px;
    left: 0px;
    right: 0;
  }


</style>



